<template>
  <b-card no-body>
    <b-card-header class="align-items-baseline">
      <feather-icon
        icon="SettingsIcon"
        size="20"
        class="text-muted cursor-pointer"
      />
    </b-card-header>
    <b-row>
      <b-col
        cols="2"
      >
        <statisticsCard
          :statistic="pendingContracts"
          icon="SettingIcon"
          :statistic-title="$t('pages.homePage.TotalTemporaryContracts')"
        />
      </b-col>
      <b-col
        cols="2"
      >
        <statisticsCard
          :statistic="acceptedContracts"
          icon="SettingIcon"
          :statistic-title="$t('pages.homePage.AcceptedContracts')"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="rejectedContracts"
          icon="SettingIcon"
          :statistic-title="$t('pages.homePage.rejectedContracts')"
          color="primary"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="closedContracts"
          icon="SettingIcon"
          :statistic-title="$t('pages.homePage.closedContracts')"
          color="primary"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="users"
          icon="SettingIcon"
          :statistic-title="$t('pages.homePage.users')"
          color="primary"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="sheikhs"
          icon="SettingIcon"
          :statistic-title="$t('pages.homePage.sheikhs')"
          color="primary"
        />
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
import {
  BCard,
  BCardHeader,
  BRow,
  BCol,
} from 'bootstrap-vue'
import statisticsCard from '@core/components/statistics-cards/StatisticCardVertical.vue'

export default {
  components: {
    BCard,
    BCardHeader,
    statisticsCard,
    BRow,
    BCol,
  },
  data() {
    return {
      acceptedContracts: 0,
      pendingContracts: 0,
      rejectedContracts: 0,
      closedContracts: 0,
      users: 0,
      sheikhs: 0,
      role: JSON.parse(localStorage.getItem('info')).role.name,
    }
  },
  created() {
    // const role = this.role === 'admin' ? 'common/fetchOrdersCount' : 'common/fetchOrdersCountUser'
    this.$store.dispatch('common/home').then(res => {
      this.acceptedContracts = res.data.data.acceptedContracts
      this.pendingContracts = res.data.data.pendingContracts
      this.rejectedContracts = res.data.data.rejectedContracts
      this.closedContracts = res.data.data.closedContracts
      this.users = res.data.data.users
      this.sheikhs = res.data.data.sheikhs
    })
  },
}
</script>

